import React, { useState } from 'react'
import Logo from './Logo';

import './style.scss'

const Header = () => {
  const isBrowser = () => typeof window !== "undefined"
  isBrowser() && window.addEventListener("scroll", function () {
    if (window.scrollY > 0 && window.scrollY < 87) {
      document.querySelector('header').className = 'scrolling';
    } else if (window.scrollY > 87) {
      document.querySelector('header').className = 'scrolling sticky-top bg-white reveal-header';
    }

    if (window.scrollY === 0) {
      document.querySelector('header').className = '';
    }
  }, { passive: true })

  const buildLink = (link) => {
    if (isBrowser() && window.location.pathname !== '/') {
      return `/${link}`;
    }
    return link;
  }

  const [showNav, setShowNav] = useState(false);

  const toggleNav = () => {
    setShowNav(!showNav);
  }

  return (
    <header>
      <nav className="navbar navbar-expand-lg navbar-light">
        <div className="container-fluid">
          <Logo />
          <a className="navbar-brand" aria-label="Home" href="/">
            <span className="text-primary">Wildtink</span>.
          </a>
          <button className="navbar-toggler" type="button" onClick={() => toggleNav()}
            aria-controls="navbartoggle" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className={(showNav ? 'show' : '') + ' collapse navbar-collapse'} id="navbartoggle">
            <div className="navbar-nav ms-lg-auto mr-3">
              <ul className="navbar-nav me-auto mb-2 mt-3 mt-lg-0 mb-lg-0">
                {/* <li className="nav-item">
                  <a className="nav-link" aria-label="Home" href={buildLink('#home')}>Home</a>
                </li> */}
                <li className="nav-item">
                  <a className="nav-link" aria-label="Work" href={buildLink('#work')}>Work</a>
                </li>
                <li className="nav-item">
                  <a className="nav-link" aria-label="About" href={buildLink('#about')}>Our Team</a>
                </li>
                <li className="nav-item">
                  <a className="nav-link" aria-label="Contact" href={buildLink('#contact')}>Contact</a>
                </li>
                {/* <li className="nav-item">
                  <a className="nav-link" aria-label="Blog" href="/blog">Blog</a>
                </li> */}
              </ul>
            </div>
          </div>
        </div>
      </nav>
    </header>
  )
};

export default Header;
