import React from "react"

import './style.scss'

const Footer = () => {
    const currentYear = (new Date()).getFullYear()

    return (
        <section>
            <div className="container">
                <div className="text-center">
                    <h4 className="text-uppercase footer-heading">
                        <span className="text-primary">Wildtinker</span> © {currentYear} All right reseved
                    </h4>
                </div>
            </div>
        </section>
    )
}

export default Footer